.wrapper {
  display: grid;
  gap: 30px;
}

.contactFormBlock {
  border: 1px solid;
  padding: 15px;
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-bottom: 30px;
  margin-right: auto;
  gap: 10px;
}
.conactFormGroup {
  display: grid;
  gap: 15px;
  margin-bottom: 5px;
  font-size: 25px;
}
.title {
  margin-left: 10px;
  font-family: Arial, Helvetica, sans-serif;
}
.label {
  font-family: Arial, Helvetica, sans-serif;
}
.input {
  margin-bottom: 30px;
  font-size: 30px;
}
.btnAddContact {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
  background-color: #fff;
  border-radius: 5px;
  cursor: pointer;
}
.btnAddContact:hover {
  background-color: blue;
}
